.container.temoignage_container{
    width: 40%;
    padding-bottom: 4rem;
}

.client_avatar{
    width: 8rem;
    aspect-ratio: 1/1;
     overflow: hidden;
     border-radius: 50%;
     margin: 0 auto 1rem;
     border: 0.4rem solid var(--color-primary-variant);
}

.temoignage{
    background: var(--color-bg-variant);
    text-align: center;
    padding: 2rem;
    border-radius: 2rem;
    user-select: none;
}

.client_review{
    color: var(--color-light);
    font-weight: 300;
    display: block;
    width: 80%;
    margin: 0.8rem auto 0;
}



/*================== MEDIA QUERY (MEDIUM DEVICES) =============== */
@media screen and (max-width: 1024px) {
    .container.temoignage_container{
        width: 60%;
    }
}
    /* ================== MEDIA QUERY (SMALL DEVICES) =============== */
    @media screen and (max-width: 600px) {
        .container.temoignage_container{
        width: var(--container-width-sm);
        height: auto;
    }
    .client_review{
        width: var(--container-width-sm);
    }
    .client_avatar{
        width: 8rem;
        
    }
    }